import React from 'react'

const Book = ({ img, title, author }) => {

    const clickHandler = () => {
        alert('hello world');
    };

    return (
        <article className='book'>
            <img src={img} alt="" className="image" />
            <h1 onClick={() => console.log(title)} > {title}</h1>
            <h4>{author}</h4>
            <button type="button" onClick={clickHandler} > hai iam saqlain </button>
        </article>
    );
}

export default Book
